.wrap {
    background-color: #282c34;
    border: 2px solid #61dafb;
    padding: 20px;
    border-radius: 5px;
    width: 400px;
    height: 150px;
    box-sizing: border-box;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button_inner {
    border: 2px solid #61dafb;
    border-radius: 5px;
    padding: 10px;
    display: flex;
    justify-content: space-around;
}